<template>
  <div>
	<el-breadcrumb separator-class="el-icon-arrow-right">
	    <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
	    <el-breadcrumb-item></el-breadcrumb-item>
	    <el-breadcrumb-item></el-breadcrumb-item>
	</el-breadcrumb>
	<el-card>
	  <el-row :gutter="10">
      <el-col :span="3">
          <div class="schoolSet"></div>
      </el-col>
	      <el-col :span="6">
	        <el-cascader  :options="options" v-model="stu_cat" :props="cateProps" @change="handleChange"></el-cascader>
	      </el-col>
        <el-col :span="3">
           <div class="schoolSet"></div>
        </el-col>
        <el-col :span="3">
          <el-autocomplete
            class="inline-input"
            v-model="state1"
            :fetch-suggestions="querySearch"
            clearable
            placeholder=""
            @change="handleAutoChange"
            @select="handleSelect">
          </el-autocomplete>
        </el-col>
        <el-col :span="2">
             <div class="schoolSet"></div>
        </el-col>
        <el-col :span="3">
            <el-select v-model="time" clearable>
            <el-option v-for="item in timeoptions" :key="item.value"  :label="item.label"  :value="item.value" >
            </el-option>
        </el-select>
        </el-col>
	      <el-col :span="4">
	         <el-button type="primary" @click="showPerson"></el-button>
	      </el-col>
	  </el-row>
    <el-row style="margin: 10px auto">
      <el-col :span="24">
         <div ref="left" style="width: 100%;height: 400px;margin: 0 auto"></div>
       </el-col>
     </el-row>
    <el-row style="margin: 10px auto">
       <el-col :span="24">
         <div ref="right" style="width: 100%;height: 400px"></div>
       </el-col>
     </el-row>
     <el-row style="margin: 10px auto">
       <el-col :span="24">
         <div ref="both" style="width: 100%;height: 400px"></div>
       </el-col>
    </el-row>
    <el-row>
      <el-col :span="24" style="font-size: 40px;padding: 20px;margin: 20rpx;text-align: center;">
        
      </el-col>
    </el-row>
   <el-row>
     <el-col :span="16" :offset="4">
       <table cellpadding="0" cellspacing="0" class="report">
         <tr>
           <th colspan="2">: {{studentWord.name}}</th>
           <th colspan="1">: {{studentWord.gender}}</th>
           <th colspan="2">:{{studentWord.birthday}}</th>
           <th colspan="3">: {{studentWord.school}}</th>
           <th colspan="2">:{{studentWord.phone}}</th>
         </tr>
         <tr>
           <td rowspan="3"></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
           <td></td>
         </tr>
         <tr>
           <td></td>
           <td>{{studentWord.farRight}}</td>
           <td>{{studentWord.nearRight}}</td>
           <td>{{studentWord.sphRight}}</td>
           <td>{{studentWord.cytRight}}</td>
           <td>{{studentWord.axisRight}}</td>
           <td>{{studentWord.correctRight}}</td>
           <td>{{studentWord.ipdRight}}</td>
           <td>{{studentWord.leadingRight}}</td>
         </tr>
         <tr>
           <td></td>
           <td>{{studentWord.farLeft}}</td>
           <td>{{studentWord.nearLeft}}</td>
           <td>{{studentWord.sphLeft}}</td>
           <td>{{studentWord.cytLeft}}</td>
           <td>{{studentWord.axisLeft}}</td>
           <td>{{studentWord.correctLeft}}</td>
           <td>{{studentWord.ipdLeft}}</td>
           <td>{{studentWord.leadingLeft}}</td>
         </tr>
         <tr>
           <td rowspan="5"></td>
           <td colspan="5">:{{studentWord.motion}}</td>
           <td colspan="4">:{{studentWord.stereopsis}}</td>
         </tr>
         <tr>
           <td colspan="5">:{{studentWord.cover}}</td>
           <td colspan="4">Worth-4:{{studentWord.worth}}</td>
         </tr>
         <tr>
           <td colspan="5">:{{studentWord.assembly}}</td>
           <td rowspan="3" ></td>
           <td colspan="3">:{{studentWord.splRight}}</td>
         </tr>
         <tr>
           <td colspan="5" rowspan="2">:{{studentWord.colourVision}}</td>
           <!-- <td ></td> -->
            <td colspan="3">:{{studentWord.splLeft}}</td>
         </tr>
         <tr>
          <!-- <td colspan="5"></td> -->
          <!-- <td ></td> -->
           <td colspan="3">:{{studentWord.splBinoculus}}</td>
         </tr>
         <!--  -->
         <tr>
           <td rowspan="3"></td>
           <td></td>
           <td colspan="2"></td>
           <td colspan="2"></td>
           <td colspan="2"></td>
           <td ></td>
           <td ></td>
         </tr>
         <tr>
           <td></td>
           <td colspan="2">{{studentWord.levelRight}}</td>
           <td colspan="2">{{studentWord.verticalRight}}</td>
           <td colspan="2">{{studentWord.axialLengthRight}}</td>
           <td >{{studentWord.acdRight}}</td>
           <td >{{studentWord.ltRight}}</td>
         </tr>
         <tr>
           <td></td>
           <td colspan="2">{{studentWord.levelLeft}}</td>
           <td colspan="2">{{studentWord.verticalLeft}}</td>
           <td colspan="2">{{studentWord.axialLengthLeft}}</td>
           <td >{{studentWord.acdLeft}}</td>
           <td >{{studentWord.ltLeft}}</td>
         </tr>
         <!--  -->
         <tr  style="text-align: left;">
           <td rowspan="2"></td>
           <td colspan="9">:{{studentWord.slitLamp}}</td>
         </tr>
         <tr>
           <td colspan="9">:{{studentWord.retCam}}</td>
         </tr>
         <!--  -->
         <tr align="left">
           <td rowspan="2"></td>
           <td colspan="9">(cm): {{studentWord.height}}</td>
         </tr>
         <tr>
           <td colspan="9">(kg):{{studentWord.weight}}</td>
         </tr>
        <!--  -->
        <tr>
          <td ></td>
          <td colspan="9">{{studentWord.suggest}}</td>
        </tr>

       </table>
     </el-col>
   </el-row>
	</el-card>
	</div>
</template>
<script>
  import echarts from 'echarts'
  import axios from 'axios'
export default {
	created() {
	  this.token = window.sessionStorage.getItem('token');
    let user = window.sessionStorage.getItem('token');
    this.identity = user.split('-') [1];
    this.fondId = user.split('-')[2];
	  this.getOPtions();
	},
    data() {
		return {
      token: '',
			stu_cat: [],
      identity: '',
      fondId: "",
			cateProps: {
			label: 'name', //
			value: 'id', // 
			children: 'children' //
			},
			options: [],
			classId: '',
			schoolId: '',
      restaurants: [],
      state1: '',
      studentList: [],
      studetId: '',
      leftLuoX: [],
      leftLuoY: [],
      rightLuoX: [],
      rightLuoY: [],
      leftZhouX: [],
      leftZhouY: [],
      rightZhouX: [],
      rightZhouY: [],
      leftQuX:[],
      leftQuY:[],
      rightQuX: [],
      rightQuY: [],
      studentWord: [],
      time: 365,
      timeoptions:[{
        value: 30 ,
        label: ''
      }, {
        value: 90,
        label: ''
      }, {
        value: 180,
        label: ''
      }, {
        value: 365,
        label: ''
      }],
		}
	},
	methods: {
    handleAutoChange(value) {
      console.log(value)
    },
	  drawLine() {
      let myChart = echarts.init(this.$refs.left);
	     this.option = {
        title: {
             text: '',
             subtext: '',
             left: 'center'
         },
        legend: {
              orient: 'vertical',
               data: ['', ''],
               left: 'left',
               y: 'top',
               top:'5%',
               right: '15%'
             },
       grid: {                   // 
               top:'8%',
               right: '10%',
             },
        xAxis: {
            type: 'category',
            symbol: 'none',
            boundaryGap: true,
            data: this.leftLuoX
        },
        yAxis: {
            type: 'value',
            min:0,
            max:2,
            splitNumber:10
        },
        series: [{
           name: '',
            data: this.leftLuoY,
            type: 'line',
            smooth: true,
             color: 'blue'
        },
        {
            name: '',
            data: this.rightLuoY,
            type: 'line',
             smooth: true,
            color: 'red'
        }]
      }
      myChart.setOption(this.option)
	   },
     drawLine3() {
       let myChart = echarts.init(this.$refs.both);
       this.option = {
         title: {
              text: '',
              subtext: '',
              left: 'center'
          },
         legend: {
                    orient: 'vertical',
                    data: ['', ''],
                    left: 'left',
                    y: 'top',
                    top:'5%',
                },
       grid: {                   // 
               top:40,
               right: '10%',
             },
        xAxis: {
               type: 'category',
               symbol: 'none',
               boundaryGap: true,
               data: this.leftQuX
         },
         grid: {                   // 
                 top:'8%',
                 right: '10%',
               },
         yAxis: {
             name: '(D)',
             type: 'value',
             min:35,
             max:55,
             splitNumber:20,
         },
         series: [{
            name: '',
             data: this.leftQuY,
             type: 'line',
             smooth: true,
             color: 'blue'
         },
         {
             name: '',
             data: this.rightQuY,
             type: 'line',
             smooth: true,
             color: 'red'
         }]
      }
       myChart.setOption(this.option)
     },
     drawLine2() {
       let myChart = echarts.init(this.$refs.right);
       this.option = {
         title: {
              text: '',
              subtext: '',
              left: 'center',
              padding:[5 , 0 ,10, 0]
          },
         legend: {
                    orient: 'vertical',
                    data: ['', ''],
                    left: 'left',
                    selectedMode:false,
                    y: 'top',
                    top:'5%',
                },
        grid: {                   // 
                top:'8%',
                right: '10%',
              },
         xAxis: {
             type: 'category',
             symbol: 'none',
             boundaryGap: true,
             data: this.leftZhouX
         },
         yAxis: {
             name: '(mm)',
             type: 'value',
             min:20,
             max:35,
             splitNumber:20
         },
         series: [{
            name: '',
             data: this.leftZhouY,
             type: 'line',
             smooth: true,
              color: 'blue'
         },
         {
             name: '',
             data: this.rightZhouY,
             type: 'line',
              smooth: true,
             color: 'red'
         }]
      }
       myChart.setOption(this.option)
     },
    //
    getStudentName() {
      let param = new URLSearchParams();
      param.append('token', this.token);
      param.append('schoolId' ,this.schoolId);
      param.append('classId', this.classId);
      axios({
        method: 'post',
        url: '/lightspace/queryStudentBySidCid',
        data: param
      }).then(this.getStudentListSucc.bind(this)).catch(this.getStudnentListErr.bind(this))
    },
    getStudentListSucc(res) {
      if(res.data.status === 10204) {
        this.$message.error(res.data.msg);
        this.$router.push('/login');
      } else if(res.data.status == 200) {
        res ? res = res.data.data: '';

        for(let i  =0; i <res.length; i++) {
          this.studentList.push({
            value: res[i].name,
            id: res[i].id
          })
        }
      }
    },
    getStudnentListErr(err) {
      console.log(err)
    },
    //
    querySearch(queryString, cb) {
      var studentList = this.studentList;
      var results = queryString ? studentList.filter(this.createFilter(queryString)) : studentList;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (student) => {
        return (student.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
       this.studentId = item.id;
     },
		//
		getOPtions() {
		    let param = new URLSearchParams();
		    param.append('token', this.token);
		    axios({
		        method: 'post',
		        url: '/lightspace/cascade1',
		        data: param
		    }).then(this.handleGetOptionSucc.bind(this)).catch(this.handleGetOptionErr.bind(this))
		},
		handleGetOptionSucc (res) {
      let that = this;
		    if(res.data.status === 10204) {
		        this.$message.error(res.data.msg);
		        this.$router.push('/login');
		    } else if(res.data.status == 200) {
		       this.options =  res.data.data;
           if(this.identity == 1) {  // admin

            }else if(this.identity == 2) {   //2 
               let selectedOption = this.options;
               let filterOption = selectedOption.filter((item, index) => {
                  if(item.id == Number(that.fondId)) {
                   return item
                 }
               })
            this.options = filterOption;
           }else if(this.identity == 3) {   // 
             this.options.forEach((item) => {
               item.children.forEach((secondItem) => {
                 if(secondItem.id == this.fondId) {
                   this.stu_cat[0]= item.id;
                   this.stu_cat[1] = secondItem.id;
                   this.schoolId = item.id;
                   this.classId = secondItem.id;
                   this.options = [];
                   item.disabled = true;
                   this.options.push(item)
                 }
               })
             })
             this.getStudentName()

           }
		    }
		},
		handleGetOptionErr(err) {
		    console.log(err)
		},
		handleChange() {
      this.state1 = ''; //
		   this.schoolId = this.stu_cat[0];
		   this.classId = this.stu_cat[1];
       this.studentList = [];
       this.studentId = '';
       this.getStudentName();
		},
    showPerson() {
        let param = new URLSearchParams();
        if(!this.token) {
          this.$message.error('');
        }else if(!this.studentId) {
          this.$message.error('')
        }else if(!this.time) {
          this.$message.error('')
        } else{
          param.append('token', this.token);
          param.append('time', this.time * 86400 )
          param.append('studentId', this.studentId);
          axios({
            method: 'post',
            data: param,
            url: '/lightspace/studentRecords'
          }).then(this.handleGetStudentRecordSucc.bind(this)).catch(this.handleGetStudentRecordErr.bind(this))
        }
    },
    handleGetStudentRecordSucc(res) {
      if(res.data.status === 10204) {
          this.$message.error(res.data.msg);
          this.$router.push('/login');
      } else if(res.data.status == 200) {

        res? res = res.data.data: '';
        if(res.studnetWord !== null) {
          this.studentWord = res.studnetWord;
        }
       // console.log(res)
        this.leftLuoX = res.visionLeft.xDataList;
        this.leftLuoY = res.visionLeft.yDataList;
        this.rightLuoX = res.visionRight.xDataList;
        this.rightLuoY = res.visionRight.yDataList;
        // this.leftZhouX = res.eyeAxisLengthLeft.xDataList;
        // this.leftZhouY = res.eyeAxisLengthLeft.yDataList;
        // this.rightZhouX = res.eyeAxisLengthRight.xDataList;
        // this.rightZhouY = res.eyeAxisLengthRight.yDataList;
        // this.leftQuX = res.curvatureLeft.xDataList;
        // this.leftQuY = res.curvatureLeft.yDataList;
        // this.rightQuX = res.curvatureRight.xDataList;
        // this.rightQuY = res.curvatureRight.yDataList;
        this.drawLine();
        this.drawLine2();
        this.drawLine3();
      }
    },
    handleGetStudentRecordErr(err) {
      console.log(err)
    }
	}
}
</script>
<style lang="less" scoped>
  .el-cascader {
    width: 100%;
  }
  .report{margin: 20px 0;}
  .report th, td {padding: 30px;text-align:center;border:1px solid #ccc; text-align: left;}
  .report tr:nth-child(2n+1) td{text-align: left;}
</style>



